@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

/* ============================================================
   Í∑ÄÏóºÎΩÄÏßù ÌÉÄÏù¥Î®∏ - ÌÜµÌï© Ïä§ÌÉÄÏùºÏãúÌä∏
   ============================================================ */

* { margin: 0; padding: 0; box-sizing: border-box; }

body {
  font-family: 'Varela Round', 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
  position: relative;
  overflow: hidden;
}

/* === Î∞∞Í≤Ω === */
.App.dark-mode {
  background: radial-gradient(circle at 20% 30%, rgba(255,182,193,0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(180,160,210,0.15) 0%, transparent 50%),
    linear-gradient(135deg, #3D3451 0%, #4A3F5C 50%, #5C4F72 100%);
}
.App.light-mode {
  background: radial-gradient(circle at 20% 30%, rgba(255,255,255,0.5) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255,182,193,0.3) 0%, transparent 50%),
    linear-gradient(135deg, #FFF0F5 0%, #F5E6FF 50%, #E8D5F5 100%);
}

.App::before { content: ''; position: fixed; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 250px; height: 250px; opacity: 0.025; pointer-events: none; z-index: 0; background: radial-gradient(circle, rgba(184,169,201,0.15) 0%, transparent 70%); border-radius: 50%; }
.App::after { content: ''; position: fixed; bottom: 30px; right: 30px; width: 60px; height: 60px; opacity: 0.06; pointer-events: none; z-index: 0; background: radial-gradient(circle, rgba(255,182,193,0.3) 0%, transparent 70%); border-radius: 50%; animation: subtleFloat 4s ease-in-out infinite; }

@keyframes subtlePulse { 0%, 100% { opacity: 0.025; } 50% { opacity: 0.04; } }

.App.timer-active.dark-mode { background: linear-gradient(135deg, #3D3451 0%, #5C4F72 100%); }
.App.timer-active.light-mode { background: linear-gradient(135deg, #FFF0F5 0%, #F5E6FF 100%); }

.container { max-width: 800px; width: 100%; position: relative; z-index: 1; }

/* === Ìó§Îçî === */
.header { text-align: center; margin-bottom: 30px; position: relative; }
.light-mode .header { color: #7B6B8D; }
.dark-mode .header { color: #FFB6C1; }
.header h1 { font-size: 3em; margin-bottom: 10px; font-weight: 700; text-shadow: 3px 3px 0px rgba(75,60,95,0.2); }
.subtitle { font-size: 1.2em; opacity: 0.9; font-weight: 600; }

.header-buttons { display: flex; gap: 12px; justify-content: center; margin-top: 25px; }

.icon-btn {
  background: #FFB6C1; border: 4px solid #7B6B8D; border-radius: 20px;
  width: 55px; height: 55px; font-size: 1.6em; cursor: pointer;
  transition: all 0.3s ease; box-shadow: 0 6px 0px #7B6B8D; position: relative;
}
.icon-btn:hover { transform: translateY(-3px) rotate(-3deg); box-shadow: 0 9px 0px #7B6B8D; }
.icon-btn:active { transform: translateY(3px); box-shadow: 0 3px 0px #7B6B8D; }

/* === Î∞∞ÎÑà === */
.dday-banner {
  background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%);
  border: 4px solid #7B6B8D; border-radius: 25px; padding: 18px 25px;
  text-align: center; color: #7B6B8D; font-size: 1.3em; margin-bottom: 18px;
  box-shadow: 0 6px 0px #7B6B8D; font-weight: 700; position: relative;
}
.dday-banner::before { content: 'üêª'; position: absolute; left: 20px; top: 50%; transform: translateY(-50%); font-size: 1.8em; }
.dday-banner::after { content: 'üçØ'; position: absolute; right: 20px; top: 50%; transform: translateY(-50%); font-size: 1.5em; }

.streak-banner {
  background: linear-gradient(135deg, #B8A9C9 0%, #D4C5E2 100%);
  border: 4px solid #7B6B8D; border-radius: 25px; padding: 18px 25px;
  text-align: center; color: white; font-size: 1.3em; margin-bottom: 18px;
  box-shadow: 0 6px 0px #7B6B8D; font-weight: 700; position: relative;
  text-shadow: 2px 2px 0px rgba(0,0,0,0.2);
}
.streak-banner::before { content: 'üß∏'; position: absolute; left: 20px; top: 50%; transform: translateY(-50%); font-size: 1.8em; }

/* === ÌÄµ Ïù∏Ìè¨ === */
.quick-info { display: flex; gap: 12px; margin-bottom: 22px; }
.quick-info-item {
  flex: 1; background: #FFF0F5; border: 4px solid #B8A9C9; border-radius: 20px;
  padding: 16px; text-align: center; color: #7B6B8D; font-size: 0.95em;
  box-shadow: 0 5px 0px #B8A9C9; font-weight: 700; position: relative;
}
.dark-mode .quick-info-item { background: rgba(255,182,193,0.25); border-color: #FFB6C1; box-shadow: 0 5px 0px #FFB6C1; color: #FFB6C1; }
.quick-info-item span { display: block; opacity: 0.85; font-size: 0.9em; margin-bottom: 5px; }
.quick-info-item strong { display: block; font-size: 1.3em; }

/* === Î™®ÏùòÍ≥†ÏÇ¨ Î≤ÑÌäº === */
.mock-exam-btn {
  width: 100%; padding: 22px;
  background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%);
  color: #7B6B8D; border: 5px solid #7B6B8D; border-radius: 30px;
  font-size: 1.4em; font-weight: 700; cursor: pointer; margin-bottom: 28px;
  transition: all 0.3s ease; box-shadow: 0 8px 0px #7B6B8D; position: relative;
}
.mock-exam-btn:hover { transform: translateY(-4px); box-shadow: 0 12px 0px #7B6B8D; }
.mock-exam-btn:active { transform: translateY(4px); box-shadow: 0 4px 0px #7B6B8D; }

/* === Í≥ºÎ™© Ïπ¥Îìú Í∑∏Î¶¨Îìú === */
.subject-grid { display: grid; grid-template-columns: repeat(auto-fit, minmax(145px, 1fr)); gap: 18px; margin-bottom: 28px; }
.subject-card {
  background: #FFF0F5; border: 4px solid; border-radius: 25px; padding: 28px 18px;
  text-align: center; cursor: pointer; transition: all 0.3s ease; box-shadow: 0 6px 0px currentColor; position: relative;
}
.dark-mode .subject-card { background: rgba(255,182,193,0.2); }
.subject-card:hover { transform: translateY(-8px) rotate(-2deg); box-shadow: 0 10px 0px currentColor; }
.subject-card:active { transform: translateY(3px); box-shadow: 0 3px 0px currentColor; }
.subject-emoji { font-size: 3.5em; margin-bottom: 12px; }
.subject-name { font-size: 1.3em; font-weight: 700; color: #7B6B8D; margin-bottom: 8px; }
.dark-mode .subject-name { color: #FFB6C1; }
.subject-time { font-size: 1.1em; font-weight: 700; color: #B8A9C9; }

/* === Ï†ïÎ≥¥ Î∞ïÏä§ === */
.info-box {
  background: #FFF0F5; border: 4px solid #B8A9C9; border-radius: 25px;
  padding: 22px 28px; color: #7B6B8D; text-align: center;
  box-shadow: 0 6px 0px #B8A9C9; position: relative;
}
.dark-mode .info-box { background: rgba(255,182,193,0.2); border-color: #FFB6C1; box-shadow: 0 6px 0px #FFB6C1; color: #FFB6C1; }
.info-box::before { content: 'üß∏'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2.5em; }
.info-box p { margin: 9px 0; font-size: 1em; font-weight: 600; line-height: 1.6; }

/* === Î™®Îã¨ === */
.modal-overlay {
  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(75,60,95,0.75); display: flex; align-items: center;
  justify-content: center; z-index: 1000; padding: 20px; backdrop-filter: blur(10px);
}
.modal-content {
  background: #FFF0F5; border: 6px solid #7B6B8D; border-radius: 35px;
  padding: 40px; max-width: 500px; width: 100%; box-shadow: 0 10px 0px #7B6B8D;
  animation: bearPop 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55); position: relative;
}
@keyframes bearPop { 0% { transform: scale(0); opacity: 0; } 100% { transform: scale(1); opacity: 1; } }
.modal-content::before { content: 'üêª'; position: absolute; top: -30px; left: 50%; transform: translateX(-50%); font-size: 3.5em; }
.modal-content h2 { text-align: center; margin-bottom: 12px; color: #7B6B8D; font-size: 1.5em; font-weight: 700; }
.modal-content p { text-align: center; color: #7B6B8D; margin-bottom: 14px; font-size: 1em; font-weight: 600; }
.modal-desc { font-size: 0.95em !important; opacity: 0.8; }

/* === Ìú¥Ïãù/Î™®ÏùòÍ≥†ÏÇ¨ Î™®Îã¨ Î≤ÑÌäº === */
.break-options { display: flex; gap: 10px; margin-bottom: 18px; }
.break-options button {
  flex: 1; padding: 18px;
  background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%);
  color: #7B6B8D; border: 4px solid #7B6B8D; border-radius: 18px;
  font-size: 1em; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #7B6B8D; transition: all 0.3s;
}
.break-options button:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #7B6B8D; }
.break-options button:active { transform: translateY(2px); box-shadow: 0 3px 0px #7B6B8D; }

.skip-btn {
  width: 100%; padding: 16px; background: #B8A9C9; color: white;
  border: 4px solid #7B6B8D; border-radius: 18px; font-size: 1em; font-weight: 700;
  cursor: pointer; transition: all 0.3s; text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
}
.skip-btn:hover { background: #D4C5E2; transform: translateY(-2px); }
.skip-btn:disabled { opacity: 0.4; cursor: not-allowed; }

.mock-exam-schedule { background: linear-gradient(135deg, #F5E6FF 0%, #E8D5F5 100%); border: 4px solid #B8A9C9; border-radius: 22px; padding: 20px; margin-bottom: 20px; }
.schedule-item { display: flex; align-items: center; justify-content: center; padding: 12px 14px; background: white; border: 3px solid #FFB6C1; border-radius: 18px; margin-bottom: 10px; text-align: center; }
.schedule-item:last-child { margin-bottom: 0; }
.schedule-subject { font-size: 1.1em; font-weight: 700; color: #7B6B8D; line-height: 1.5; }
.total-time { text-align: center; color: #7B6B8D; font-weight: 700; margin-bottom: 28px; font-size: 1.1em; }

.modal-buttons { display: flex; gap: 10px; }
.start-mock-btn {
  flex: 1; padding: 18px; background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%);
  color: #7B6B8D; border: 4px solid #7B6B8D; border-radius: 18px;
  font-size: 1.2em; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #7B6B8D; transition: all 0.3s;
}
.start-mock-btn:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #7B6B8D; }
.cancel-btn {
  flex: 1; padding: 18px; background: #B8A9C9; color: white;
  border: 4px solid #7B6B8D; border-radius: 18px; font-size: 1.2em; font-weight: 700;
  cursor: pointer; transition: all 0.3s; text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
}
.cancel-btn:hover { background: #D4C5E2; transform: translateY(-2px); }

/* === Ïπ¥Ïö¥Ìä∏Îã§Ïö¥ ÌôîÎ©¥ === */
.ready-container { max-width: 600px; width: 100%; text-align: center; color: #7B6B8D; }
.dark-mode .ready-container { color: #FFB6C1; }
.ready-subject { font-size: 3.5em; font-weight: 700; margin-bottom: 32px; animation: bearSwing 2s ease-in-out infinite; }
@keyframes bearSwing { 0%, 100% { transform: rotate(-3deg); } 50% { transform: rotate(3deg); } }
.ready-tip {
  background: #FFF0F5; border: 5px solid #B8A9C9; border-radius: 28px;
  padding: 32px; font-size: 1.3em; line-height: 1.7; margin-bottom: 40px;
  box-shadow: 0 8px 0px #B8A9C9; font-weight: 600; color: #7B6B8D; position: relative;
}
.dark-mode .ready-tip { background: rgba(255,182,193,0.25); border-color: #FFB6C1; box-shadow: 0 8px 0px #FFB6C1; color: #FFB6C1; }
.ready-tip::before { content: 'üçØ'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2.5em; }
.ready-countdown { font-size: 9em; font-weight: 700; margin-bottom: 22px; animation: bearHop 1s infinite; }
@keyframes bearHop { 0%, 100% { transform: scale(1); } 50% { transform: scale(1.15); } }
.ready-message { font-size: 1.6em; font-weight: 700; }

/* === ÌÉÄÏù¥Î®∏ ÌôîÎ©¥ === */
.timer-container { max-width: 600px; width: 100%; position: relative; }
.mock-exam-progress {
  text-align: center; color: #7B6B8D; font-size: 0.95em; margin-bottom: 10px; padding: 10px;
  background: #FFF0F5; border: 4px solid #B8A9C9; border-radius: 22px; font-weight: 700; box-shadow: 0 5px 0px #B8A9C9;
}
.dark-mode .mock-exam-progress { background: rgba(255,182,193,0.25); border-color: #FFB6C1; box-shadow: 0 5px 0px #FFB6C1; color: #FFB6C1; }
.timer-header { text-align: center; margin-bottom: 10px; }
.timer-header h2 { color: #7B6B8D; font-size: 1.6em; font-weight: 700; }
.dark-mode .timer-header h2 { color: #FFB6C1; }

.timer-display {
  background: #FFF0F5; border: 8px solid #B8A9C9; border-radius: 35px;
  padding: 40px 30px; text-align: center; margin-bottom: 12px;
  box-shadow: 0 10px 0px #B8A9C9; transition: all 0.3s ease; position: relative;
}
.dark-mode .timer-display { background: rgba(255,182,193,0.3); border-color: #FFB6C1; box-shadow: 0 10px 0px #FFB6C1; }
.timer-display::before { content: 'üêª'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2em; }
.timer-display.break-mode { background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%); border-color: #7B6B8D; }
.timer-display.break-mode::before { content: 'üçØ'; }
.timer-display.warning { background: linear-gradient(135deg, #FFF9E6 0%, #FFE9B3 100%); }
.timer-display.critical { background: linear-gradient(135deg, #FFE6E6 0%, #FFB3B3 100%); }
@keyframes warningGlow { 0%, 100% { box-shadow: 0 10px 0px #FFD1DC; } 50% { box-shadow: 0 10px 15px rgba(255,209,220,0.5); } }
@keyframes criticalGlow { 0%, 100% { box-shadow: 0 10px 0px #FF6B6B; } 50% { box-shadow: 0 10px 20px rgba(255,107,107,0.4); } }

.time { font-size: 7em; font-weight: 700; color: #7B6B8D; font-variant-numeric: tabular-nums; letter-spacing: 0.05em; }
.dark-mode .time { color: #FFB6C1; }
.timer-display.break-mode .time { color: #7B6B8D; }
.alert-text { margin-top: 10px; font-size: 1.1em; font-weight: 700; color: #FFA500; }
.break-text { margin-top: 10px; font-size: 1.2em; font-weight: 700; color: #7B6B8D; }
.critical-alert { color: #FF6B6B; animation: bearBlink 0.5s infinite; }
@keyframes bearBlink { 0%, 100% { opacity: 1; } 50% { opacity: 0.5; } }

.focus-score-live { margin-top: 10px; font-size: 1.1em; font-weight: 700; color: #7B6B8D; animation: scorePulse 2s ease-in-out infinite; }
.dark-mode .focus-score-live { color: #FFB6C1; }
.timer-display.break-mode .focus-score-live { display: none; }
@keyframes scorePulse { 0%, 100% { opacity: 1; transform: scale(1); } 50% { opacity: 0.85; transform: scale(1.02); } }

.progress-bar { height: 20px; background: #E8D5F5; border: 3px solid #B8A9C9; border-radius: 12px; overflow: hidden; margin-bottom: 10px; box-shadow: inset 0 3px 6px rgba(75,60,95,0.2); }
.dark-mode .progress-bar { background: rgba(155,137,180,0.3); border-color: #FFB6C1; }
.progress-fill { height: 100%; transition: width 0.3s ease; border-radius: 18px; background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%); box-shadow: inset 0 -2px 6px rgba(255,255,255,0.5); }

.timer-controls { display: flex; gap: 8px; justify-content: center; position: relative; z-index: 1000; }
.btn { padding: 12px 28px; font-size: 1em; font-weight: 700; border: 4px solid #7B6B8D; border-radius: 16px; cursor: pointer; transition: all 0.3s ease; box-shadow: 0 6px 0px #7B6B8D; }
.btn:hover { transform: translateY(-3px); box-shadow: 0 9px 0px #7B6B8D; }
.btn:active { transform: translateY(3px); box-shadow: 0 3px 0px #7B6B8D; }
.btn-pause { background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%); color: #7B6B8D; }
.btn-stop { background: #B8A9C9; color: white; text-shadow: 1px 1px 2px rgba(0,0,0,0.2); }

/* ÌÉÄÏù¥Î®∏ Ïª¥Ìå©Ìä∏ Î≤ÑÌäº */
.btn-sm {
  padding: 10px 24px; font-size: 1.2em; font-weight: 700; border: 3px solid #7B6B8D;
  border-radius: 14px; cursor: pointer; transition: all 0.2s; box-shadow: 0 4px 0px #7B6B8D;
}
.btn-sm:hover { transform: translateY(-2px); box-shadow: 0 6px 0px #7B6B8D; }
.btn-sm:active { transform: translateY(2px); box-shadow: 0 2px 0px #7B6B8D; }

/* === ÏùºÏãúÏ†ïÏßÄ & ÏôÑÎ£å Ïò§Î≤ÑÎ†àÏù¥ === */
.paused-overlay, .completion-overlay {
  position: fixed; top: 0; left: 0; width: 100vw; height: 100vh;
  z-index: 500; display: flex; flex-direction: column; justify-content: center; align-items: center;
}
.paused-overlay { background-color: rgba(0,0,0,0.8); }
.completion-overlay { background: linear-gradient(135deg, rgba(75,60,95,0.95) 0%, rgba(155,137,180,0.95) 100%); animation: bearFadeIn 0.3s ease; }
@keyframes bearFadeIn { from { opacity: 0; } to { opacity: 1; } }
.pause-main { font-size: 4rem; font-weight: 900; color: #ffffff; margin-bottom: 20px; text-shadow: 0 4px 10px rgba(0,0,0,0.5); }
.pause-tip { font-size: 1.5rem; color: #FFB6C1; background: rgba(255,255,255,0.1); padding: 15px 30px; border-radius: 50px; max-width: 80%; text-align: center; }
.completion-message { color: #FFB6C1; font-size: 2.5em; font-weight: 700; text-align: center; animation: bearCelebrate 1s ease infinite; padding: 0 40px; line-height: 1.6; white-space: pre-line; }
@keyframes bearCelebrate { 0%, 100% { transform: scale(1) rotate(-2deg); } 25% { transform: scale(1.1) rotate(2deg); } 50% { transform: scale(1) rotate(-2deg); } 75% { transform: scale(1.1) rotate(2deg); } }

/* === Ï≤¥ÌÅ¨Î¶¨Ïä§Ìä∏ === */
.checklist-header, .notes-header, .stats-header, .settings-header { margin-bottom: 12px; }
.checklist-progress { text-align: center; color: #7B6B8D; font-size: 0.9em; margin-top: 6px; font-weight: 700; }
.dark-mode .checklist-progress { color: #FFB6C1; }
.checklist-input-box { display: flex; gap: 8px; margin-bottom: 10px; }
.checklist-input-box input { flex: 1; padding: 10px 14px; border: 3px solid #B8A9C9; border-radius: 18px; font-size: 15px; outline: none; font-weight: 600; background: #FFF0F5; color: #7B6B8D; }
.dark-mode .checklist-input-box input { background: rgba(255,182,193,0.2); color: #FFB6C1; border-color: #FFB6C1; }
.checklist-input-box input::placeholder { color: #B8A9C9; opacity: 0.7; }
.checklist-input-box button { padding: 10px 20px; background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%); color: #7B6B8D; border: 4px solid #7B6B8D; border-radius: 18px; font-size: 15px; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #7B6B8D; transition: all 0.3s; }
.checklist-input-box button:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #7B6B8D; }
.checklist-list { background: #FFF0F5; border: 4px solid #B8A9C9; border-radius: 16px; padding: 14px; box-shadow: 0 8px 0px #B8A9C9; min-height: 300px; }
.dark-mode .checklist-list { background: rgba(255,182,193,0.2); border-color: #FFB6C1; box-shadow: 0 8px 0px #FFB6C1; }
.empty-message { text-align: center; color: #7B6B8D; font-size: 1em; padding: 30px 15px; font-weight: 700; }
.dark-mode .empty-message { color: #FFB6C1; }
.checklist-item { display: flex; align-items: center; justify-content: space-between; padding: 10px 0; border-bottom: 2px dashed #B8A9C9; }
.dark-mode .checklist-item { border-bottom-color: #FFB6C1; }
.checklist-item:last-child { border-bottom: none; }
.checklist-content { display: flex; align-items: center; gap: 16px; flex: 1; cursor: pointer; user-select: none; }
.checkbox { width: 30px; height: 30px; border: 4px solid #B8A9C9; border-radius: 8px; display: flex; align-items: center; justify-content: center; font-size: 16px; transition: all 0.3s; background: white; }
.dark-mode .checkbox { border-color: #FFB6C1; background: rgba(255,182,193,0.2); }
.checkbox.checked { background: linear-gradient(135deg, #FFB6C1 0%, #FFD1DC 100%); border-color: #7B6B8D; transform: scale(1.1) rotate(5deg); }
.checklist-content span { font-size: 1.1em; color: #7B6B8D; transition: all 0.3s; font-weight: 600; }
.dark-mode .checklist-content span { color: #FFB6C1; }
.checklist-content span.completed { text-decoration: line-through; opacity: 0.5; }
.delete-btn { background: none; border: none; font-size: 26px; cursor: pointer; padding: 8px; transition: all 0.3s; opacity: 0.7; }
.delete-btn:hover { transform: scale(1.3) rotate(12deg); opacity: 1; }

/* === ÎÖ∏Ìä∏ === */
.notes-tabs { display: flex; gap: 6px; flex-wrap: wrap; margin-bottom: 10px; justify-content: center; }
.note-tab { padding: 8px 14px; background: #FFF0F5; border: 3px solid #B8A9C9; border-radius: 12px; color: #7B6B8D; font-size: 0.85em; cursor: pointer; font-weight: 700; box-shadow: 0 4px 0px #B8A9C9; transition: all 0.3s; }
