/* Í≥ºÎ™©Î≥Ñ ÏÉâÏÉÅ ÌÖåÎßà */
.timer-active { background: var(--subject-glow, transparent); transition: background 0.5s; }

/* ÎèôÍ∏∞Î∂ÄÏó¨ ÌÜ†Ïä§Ìä∏ */
.motivation-toast {
  position: fixed; top: 20px; left: 50%; transform: translateX(-50%);
  background: linear-gradient(135deg, #FFB6C1, #FFD1DC); color: #7B6B8D;
  padding: 12px 20px; border-radius: 16px; font-weight: 700; font-size: 0.9em;
  border: 3px solid #7B6B8D; box-shadow: 0 4px 15px rgba(0,0,0,0.2);
  z-index: 1000; animation: toastSlide 4s ease-in-out forwards;
  text-align: center; max-width: 90%;
}
@keyframes toastSlide {
  0% { opacity: 0; top: -50px; } 10% { opacity: 1; top: 20px; }
  80% { opacity: 1; top: 20px; } 100% { opacity: 0; top: -50px; }
}

/* Ïª®ÎîîÏÖò ÏÑ†ÌÉù */
.condition-select { margin: 12px 0; }
.condition-options { display: flex; justify-content: center; gap: 8px; }
.condition-btn {
  display: flex; flex-direction: column; align-items: center; gap: 2px;
  padding: 8px 14px; border: 3px solid #D4C5E2; border-radius: 12px;
  background: #FFF0F5; cursor: pointer; transition: all 0.2s; color: #7B6B8D; font-weight: 600;
}
.condition-btn.active { border-color: #7B6B8D; background: #FFB6C1; transform: scale(1.08); box-shadow: 0 3px 0px #7B6B8D; }
.dark-mode .condition-btn { background: rgba(255,182,193,0.15); color: #FFB6C1; border-color: #FFB6C1; }
.dark-mode .condition-btn.active { background: #FFB6C1; color: #7B6B8D; }

/* ÏÇ¨Ïö¥Îìú Ï§ÄÎπÑ ÌôîÎ©¥ */
.sound-prep-mascot { width: 100px; height: 100px; border-radius: 50%; object-fit: cover; border: 3px solid #B8A9C9; animation: mascotBounce 1s ease-in-out infinite; box-shadow: 0 6px 20px rgba(123,107,141,0.3); }
@keyframes subtleFloat { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-5px); } }
