/* ============================================================
   Ï∫êÎ¶≠ÌÑ∞ ÎßàÏä§ÏΩîÌä∏ Ïä§ÌÉÄÏùº
   ============================================================ */
.header-mascot { display: flex; justify-content: center; margin-bottom: 8px; }
.mascot-img { width: 80px; height: 80px; border-radius: 50%; border: 3px solid #B8A9C9; cursor: pointer; transition: transform 0.3s; object-fit: cover; box-shadow: 0 4px 12px rgba(123,107,141,0.2); }
.mascot-img:hover { transform: scale(1.1) rotate(5deg); }
.mascot-img:active { transform: scale(0.95); }

.ready-mascot { width: 120px; height: 120px; border-radius: 50%; border: 4px solid #B8A9C9; margin-bottom: 16px; object-fit: cover; animation: mascotBounce 1s ease-in-out infinite; box-shadow: 0 6px 20px rgba(123,107,141,0.3); }
@keyframes mascotBounce { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-10px); } }

.pause-mascot { width: 100px; height: 100px; border-radius: 50%; border: 3px solid rgba(255,255,255,0.3); margin-bottom: 16px; object-fit: cover; opacity: 0.8; animation: mascotBounce 2s ease-in-out infinite; }

.toast-mascot { width: 36px; height: 36px; border-radius: 50%; margin-right: 10px; object-fit: cover; vertical-align: middle; }

.dark-mode .mascot-img { border-color: #FFB6C1; box-shadow: 0 4px 12px rgba(255,182,193,0.2); }
.dark-mode .ready-mascot { border-color: #FFB6C1; box-shadow: 0 6px 20px rgba(255,182,193,0.3); }

/* Î™®Îã¨/ÏôÑÎ£å ÎßàÏä§ÏΩîÌä∏ */
.modal-mascot { width: 70px; height: 70px; border-radius: 50%; object-fit: cover; display: block; margin: 0 auto 12px; border: 3px solid #B8A9C9; box-shadow: 0 4px 12px rgba(123,107,141,0.2); }
.completion-mascot { width: 90px; height: 90px; border-radius: 50%; object-fit: cover; margin-bottom: 16px; border: 3px solid rgba(255,255,255,0.4); animation: mascotBounce 1s ease-in-out infinite; }
.dark-mode .modal-mascot { border-color: #FFB6C1; }

/* ÏÑúÎ∏åÌôîÎ©¥ Ìó§Îçî Ï∫êÎ¶≠ÌÑ∞ */
.sub-header-mascot { width: 36px; height: 36px; border-radius: 50%; object-fit: cover; opacity: 0.5; position: absolute; right: 16px; top: 50%; transform: translateY(-50%); }
.checklist-header, .settings-header, .stats-header, .sub-header.compact { position: relative; }

/* ÌÉÄÏù¥Î®∏ ÏΩîÎÑà Ï∫êÎ¶≠ÌÑ∞ */
.timer-corner-mascot { position: absolute; bottom: 20px; right: 20px; width: 50px; height: 50px; border-radius: 50%; object-fit: cover; opacity: 0.12; pointer-events: none; animation: subtleFloat 3s ease-in-out infinite; }
